<template>
  <view class="history-page" :style="{ paddingTop: containerPaddingTop }">
    <!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
    <view class="custom-navbar">
      <!-- Ê∏êÂèòËÉåÊôØ -->
      <view class="navbar-gradient-bg"></view>
      <!-- Áä∂ÊÄÅÊ†èÂç†‰Ωç -->
      <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
      <!-- ÂØºËà™Ê†èÂÜÖÂÆπ -->
      <view class="navbar-content" :style="{ height: navBarHeight + 'px' }">
        <view class="navbar-left" @click="goBack">
          <text class="back-icon">‚Üê</text>
        </view>
        <view class="navbar-title">
          <text class="title-text">ÂÜô‰ø°ËÆ∞ÂΩï</text>
        </view>
        <view class="navbar-right"></view>
      </view>
    </view>

    <!-- ÂàÜÁ±ªÊ†áÁ≠æ -->
    <view class="category-tabs">
      <view 
        class="tab-item" 
        :class="{ active: activeTab === 'all' }"
        @click="switchTab('all')"
      >
        <text>ÂÖ®ÈÉ®</text>
      </view>
      <view 
        class="tab-item" 
        :class="{ active: activeTab === 'unsent' }"
        @click="switchTab('unsent')"
      >
        <text>Êú™ÂèëÈÄÅ</text>
      </view>
      <view 
        class="tab-item" 
        :class="{ active: activeTab === 'sent' }"
        @click="switchTab('sent')"
      >
        <text>Â∑≤ÂèëÈÄÅ</text>
      </view>
    </view>

    <!-- ‰ø°‰ª∂ÂàóË°® -->
    <view v-if="filteredLetters.length > 0" class="letter-list">
      <view 
        v-for="(letter, index) in filteredLetters" 
        :key="letter.id || index"
        class="letter-card"
        @click="viewLetter(letter, index)"
      >
        <!-- Áº©Áï•ÂõæÈ¢ÑËßà -->
        <view class="card-preview">
          <image 
            class="preview-bg" 
            :src="getLetterBackground(letter)" 
            mode="aspectFill"
          ></image>
          <view class="preview-overlay" :style="{ opacity: 1 - letter.opacity / 100 }"></view>
          <view class="preview-badge">
            <text class="badge-text">{{ letter.style === 'custom' ? 'Ëá™ÂÆö‰πâ' : 'Ê†∑Âºè' + letter.style }}</text>
          </view>
        </view>

        <!-- ‰ø°‰ª∂‰ø°ÊÅØ -->
        <view class="card-info">
          <text class="card-title">{{ letter.title }}</text>
          <view class="card-meta">
            <text class="meta-item">üìÖ ÈÄÅËææ: {{ letter.deliveryDate }}</text>
            <text class="meta-item">üìù {{ letter.createTime }}</text>
            <view class="meta-item">
              <text class="status-badge" :class="getStatusClass(letter.status)">
                {{ getStatusText(letter.status) }}
              </text>
            </view>
          </view>
          <view class="card-preview-content">
            <text class="preview-text">{{ letter.content.slice(0, 50) }}{{ letter.content.length > 50 ? '...' : '' }}</text>
          </view>
        </view>

        <!-- Êìç‰ΩúÊåâÈíÆ -->
        <view class="card-actions">
          <view class="action-btn view" @click.stop="viewLetter(letter, index)">
            <text>üëÅÔ∏è Êü•Áúã</text>
          </view>
          <view class="action-btn delete" @click.stop="confirmDelete(letter, index)">
            <text>üóëÔ∏è Âà†Èô§</text>
          </view>
        </view>
      </view>
    </view>

    <!-- Á©∫Áä∂ÊÄÅ -->
    <view v-else class="empty-state">
      <text class="empty-icon">‚úâÔ∏è</text>
      <text class="empty-text">{{ emptyText }}</text>
      <button v-if="activeTab !== 'sent'" class="write-btn" @click="goWrite">ÂÜôÁ¨¨‰∏ÄÂ∞Å‰ø°</button>
    </view>

    <!-- ‰ø°‰ª∂ËØ¶ÊÉÖÂºπÁ™ó -->
    <view v-if="showDetailModal" class="detail-modal-overlay" @click="closeDetail">
      <view class="detail-modal-content" @click.stop>
        <text class="detail-modal-title">‰ø°‰ª∂ËØ¶ÊÉÖ</text>
        
        <!-- ‰ø°‰ª∂ËûçÂêàÈ¢ÑËßà -->
        <view class="letter-detail">
          <view class="letter-wrapper">
            <!-- Â∫ïÂõæÂ±Ç -->
            <image 
              class="letter-bg" 
              :src="getLetterBackground(currentLetter)" 
              mode="aspectFill"
            ></image>
            
            <!-- ÈÄèÊòéÂ∫¶ÈÅÆÁΩ© -->
            <view class="letter-overlay" :style="{ opacity: 1 - currentLetter.opacity / 100 }"></view>
            
            <!-- ‰ø°‰ª∂ÂÜÖÂÆπÂ±Ç -->
            <view class="letter-content">
              <view class="letter-header">
                <text class="letter-title">{{ currentLetter.title }}</text>
                <text class="letter-date">ÈÄÅËææÊó∂Èó¥Ôºö{{ currentLetter.deliveryDate }}</text>
              </view>
              
              <view class="letter-body">
                <text class="letter-text">{{ currentLetter.content }}</text>
              </view>
              
              <view class="letter-footer">
                <text class="letter-info">‚Äî‚Äî ÁªôÊú™Êù•ÁöÑ‰Ω†</text>
                <text class="letter-time">ÂàõÂª∫‰∫é {{ currentLetter.createTime }}</text>
              </view>
            </view>
          </view>
        </view>

        <view class="detail-modal-actions">
          <button class="detail-modal-btn close" @click="closeDetail">ÂÖ≥Èó≠</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { 
  getFutureLetterList, 
  getSentLetters,
  deleteFutureLetter,
  getFutureLetterDetail
} from '@/api/futureLetter.js';

export default {
  data() {
    return {
      statusBarHeight: 0,
      navBarHeight: 44,
      screenWidth: 375,
      letters: [],
      sentLetters: [],
      activeTab: 'all',
      showDetailModal: false,
      currentLetter: null,
      currentIndex: -1
    };
  },
  computed: {
    containerPaddingTop() {
      const totalHeightPx = this.statusBarHeight + this.navBarHeight;
      const pxToRpx = 750 / this.screenWidth;
      const totalHeightRpx = totalHeightPx * pxToRpx;
      return totalHeightRpx + 20 + 'rpx';
    },
    filteredLetters() {
      if (this.activeTab === 'all') {
        return [...this.letters, ...this.sentLetters];
      } else if (this.activeTab === 'unsent') {
        return this.letters;
      } else if (this.activeTab === 'sent') {
        return this.sentLetters;
      }
      return [];
    },
    emptyText() {
      if (this.activeTab === 'sent') {
        return 'ËøòÊ≤°ÊúâÂ∑≤ÂèëÈÄÅÁöÑ‰ø°‰ª∂';
      } else if (this.activeTab === 'unsent') {
        return 'ËøòÊ≤°ÊúâÊú™ÂèëÈÄÅÁöÑ‰ø°‰ª∂';
      }
      return 'ËøòÊ≤°ÊúâÂÜôËøá‰ø°‰ª∂';
    }
  },
  onLoad() {
    this.getSystemInfo();
    this.loadLetters();
  },
  onShow() {
    this.loadLetters();
  },
  methods: {
    goBack() {
      uni.navigateBack();
    },
    getSystemInfo() {
      const systemInfo = uni.getSystemInfoSync();
      this.statusBarHeight = systemInfo.statusBarHeight || 0;
      this.screenWidth = systemInfo.windowWidth || 375;
      // #ifdef MP-WEIXIN
      this.navBarHeight = 44;
      // #endif
      // #ifdef H5
      this.navBarHeight = 44;
      // #endif
    },
    // ÂàáÊç¢Ê†áÁ≠æ
    switchTab(tab) {
      this.activeTab = tab;
    },
    
    // Âä†ËΩΩ‰ø°‰ª∂ÂàóË°®
    async loadLetters() {
      try {
        // Âä†ËΩΩÊú™ÂèëÈÄÅÁöÑ‰ø°‰ª∂ÔºàËçâÁ®øÂíåÂ∑≤ÂÆâÊéíÔºâ
        const response = await getFutureLetterList();
        
        if (response && response.data) {
          // ËΩ¨Êç¢ÂêéÁ´ØÊï∞ÊçÆÊ†ºÂºè‰∏∫ÂâçÁ´ØÊòæÁ§∫Ê†ºÂºè
          const backendLetters = Array.isArray(response.data) ? response.data : [];
          this.letters = backendLetters
            .filter(letter => letter.status !== 'SENT') // ËøáÊª§ÊéâÂ∑≤ÂèëÈÄÅÁöÑ
            .map(letter => ({
              id: letter.id,
              title: letter.title,
              content: letter.content,
              deliveryDate: letter.scheduledDate, // ÂêéÁ´ØÂ≠óÊÆµÂêç
              createTime: letter.createdAt || letter.createTime,
              status: letter.status,
              style: this.getStyleFromBackground(letter.backgroundImage),
              customImage: letter.backgroundImage,
              opacity: 100, // ÈªòËÆ§ÈÄèÊòéÂ∫¶
              // ‰øùÁïôÂêéÁ´ØÂéüÂßãÊï∞ÊçÆ
              _backendData: letter
            }));
        } else {
          // Â¶ÇÊûúÂêéÁ´ØÊ≤°ÊúâÊï∞ÊçÆÔºåÂ∞ùËØï‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩÔºàÂÖºÂÆπÊóßÊï∞ÊçÆÔºâ
          const localLetters = uni.getStorageSync('xinxiang_letters') || [];
          this.letters = localLetters.filter(letter => letter.status !== 'SENT');
        }
      } catch (error) {
        console.error('Âä†ËΩΩ‰ø°‰ª∂Â§±Ë¥•', error);
        // Â¶ÇÊûúAPIË∞ÉÁî®Â§±Ë¥•ÔºåÂ∞ùËØï‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩÔºàÈôçÁ∫ßÊñπÊ°àÔºâ
        try {
          const localLetters = uni.getStorageSync('xinxiang_letters') || [];
          this.letters = localLetters.filter(letter => letter.status !== 'SENT');
        } catch (e) {
          console.error('Âä†ËΩΩÊú¨Âú∞‰ø°‰ª∂Â§±Ë¥•', e);
          this.letters = [];
        }
        
        // ÊòæÁ§∫ÈîôËØØÊèêÁ§∫ÔºàÈùûÂÖ≥ÈîÆÈîôËØØÔºå‰∏çÈòªÂ°ûÁî®Êà∑Ôºâ
        if (error.statusCode !== 401) {
          // 401ÈîôËØØÁî±http.jsÁªü‰∏ÄÂ§ÑÁêÜÔºåËøôÈáå‰∏çÈáçÂ§çÊèêÁ§∫
          console.warn('‰ªéÂêéÁ´ØÂä†ËΩΩ‰ø°‰ª∂Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞Êï∞ÊçÆ');
        }
      }
      
      // Âä†ËΩΩÂ∑≤ÂèëÈÄÅÁöÑ‰ø°‰ª∂
      try {
        const sentResponse = await getSentLetters();
        if (sentResponse && sentResponse.data) {
          const backendSentLetters = Array.isArray(sentResponse.data) ? sentResponse.data : [];
          this.sentLetters = backendSentLetters.map(letter => ({
            id: letter.id,
            title: letter.title,
            content: letter.content,
            deliveryDate: letter.scheduledDate,
            createTime: letter.createdAt || letter.createTime,
            sentAt: letter.sentAt,
            status: letter.status,
            style: this.getStyleFromBackground(letter.backgroundImage),
            customImage: letter.backgroundImage,
            opacity: 100,
            _backendData: letter
          }));
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂ∑≤ÂèëÈÄÅ‰ø°‰ª∂Â§±Ë¥•', error);
        if (error.statusCode !== 401) {
          console.warn('‰ªéÂêéÁ´ØÂä†ËΩΩÂ∑≤ÂèëÈÄÅ‰ø°‰ª∂Â§±Ë¥•');
        }
      }
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÊñáÊú¨
    getStatusText(status) {
      const statusMap = {
        'DRAFT': 'ËçâÁ®ø',
        'SCHEDULED': 'Â∑≤ÂÆâÊéí',
        'SENT': 'Â∑≤ÂèëÈÄÅ'
      };
      return statusMap[status] || status;
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ª
    getStatusClass(status) {
      const classMap = {
        'DRAFT': 'status-draft',
        'SCHEDULED': 'status-scheduled',
        'SENT': 'status-sent'
      };
      return classMap[status] || '';
    },
    
    // ‰ªéËÉåÊôØÂõæÁâáURLÊèêÂèñÊ†∑ÂºèID
    getStyleFromBackground(backgroundImage) {
      if (!backgroundImage) return 1;
      // Â¶ÇÊûúÊòØËá™ÂÆö‰πâÂõæÁâáÔºåËøîÂõû'custom'
      if (backgroundImage.includes('custom') || backgroundImage.startsWith('http')) {
        return 'custom';
      }
      // ‰ªéË∑ØÂæÑ‰∏≠ÊèêÂèñÊ†∑ÂºèÁºñÂè∑ÔºåÂ¶Ç /static/xinxiang/xin1.jpg -> 1
      const match = backgroundImage.match(/xin(\d+)\.jpg/);
      return match ? parseInt(match[1]) : 1;
    },
    
    // Ëé∑Âèñ‰ø°‰ª∂ËÉåÊôØÂõæ
    getLetterBackground(letter) {
      if (letter.style === 'custom') {
        return letter.customImage;
      }
      return `../../static/xinxiang/xin${letter.style}.jpg`;
    },
    
    // Êü•Áúã‰ø°‰ª∂ËØ¶ÊÉÖ
    async viewLetter(letter, index) {
      try {
        // ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
        uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' });
        
        // Ë∞ÉÁî®ËØ¶ÊÉÖÊé•Âè£Ëé∑ÂèñÂÆåÊï¥‰ø°ÊÅØ
        const response = await getFutureLetterDetail(letter.id);
        
        uni.hideLoading();
        
        // Â§ÑÁêÜÂìçÂ∫îÊï∞ÊçÆ
        if (response && response.data) {
          const detailData = response.data;
          // ÂêàÂπ∂ËØ¶ÊÉÖÊï∞ÊçÆÂà∞ÂΩìÂâç‰ø°‰ª∂ÂØπË±°
          this.currentLetter = {
            ...letter,
            ...detailData,
            // Á°Æ‰øùÂ≠óÊÆµÊò†Â∞ÑÊ≠£Á°Æ
            id: detailData.id || letter.id,
            title: detailData.title || letter.title,
            content: detailData.content || letter.content,
            deliveryDate: detailData.scheduledDate || detailData.deliveryDate || letter.deliveryDate,
            createTime: detailData.createdAt || detailData.createTime || letter.createTime,
            sentAt: detailData.sentAt || letter.sentAt,
            status: detailData.status || letter.status,
            style: this.getStyleFromBackground(detailData.backgroundImage || letter.backgroundImage),
            customImage: detailData.backgroundImage || letter.customImage,
            opacity: detailData.opacity !== undefined ? detailData.opacity : (letter.opacity || 100),
            _backendData: detailData
          };
        } else {
          // Â¶ÇÊûúËØ¶ÊÉÖÊé•Âè£Â§±Ë¥•Ôºå‰ΩøÁî®ÂàóË°®Êï∞ÊçÆ
          this.currentLetter = letter;
        }
        
        this.currentIndex = index;
        this.showDetailModal = true;
      } catch (error) {
        uni.hideLoading();
        console.error('Ëé∑Âèñ‰ø°‰ª∂ËØ¶ÊÉÖÂ§±Ë¥•', error);
        
        // Â¶ÇÊûúËØ¶ÊÉÖÊé•Âè£Â§±Ë¥•Ôºå‰ΩøÁî®ÂàóË°®Êï∞ÊçÆ‰Ωú‰∏∫ÈôçÁ∫ßÊñπÊ°à
        this.currentLetter = letter;
        this.currentIndex = index;
        this.showDetailModal = true;
        
        // ÊòæÁ§∫ÈîôËØØÊèêÁ§∫ÔºàÈùûÈòªÂ°ûÔºâ
        uni.showToast({
          title: 'Âä†ËΩΩËØ¶ÊÉÖÂ§±Ë¥•ÔºåÊòæÁ§∫Âü∫Êú¨‰ø°ÊÅØ',
          icon: 'none',
          duration: 2000
        });
      }
    },
    
    // ÂÖ≥Èó≠ËØ¶ÊÉÖÂºπÁ™ó
    closeDetail() {
      this.showDetailModal = false;
      this.currentLetter = null;
      this.currentIndex = -1;
    },
    
    // Á°ÆËÆ§Âà†Èô§
    confirmDelete(letter, index) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÂ∞Å‰ø°‰ª∂ÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.deleteLetter(letter, index);
          }
        }
      });
    },
    
    // Âà†Èô§‰ø°‰ª∂
    async deleteLetter(letter, index) {
      const isInSent = this.sentLetters.some(l => l.id === letter.id);
      const sourceList = isInSent ? this.sentLetters : this.letters;
      const sourceIndex = sourceList.findIndex(l => l.id === letter.id);
      
      // Â¶ÇÊûúÊúâÂêéÁ´ØIDÔºåË∞ÉÁî®ÂêéÁ´ØAPIÂà†Èô§
      if (letter && letter.id) {
        try {
          uni.showLoading({ title: 'Ê≠£Âú®Âà†Èô§...' });
          await deleteFutureLetter(letter.id);
          uni.hideLoading();
          
          // ‰ªéÂàóË°®‰∏≠ÁßªÈô§
          if (sourceIndex !== -1) {
            sourceList.splice(sourceIndex, 1);
          }
          
          // ÂêåÊó∂Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®ÔºàÂ¶ÇÊûúÂ≠òÂú®Ôºâ
          try {
            const localLetters = uni.getStorageSync('xinxiang_letters') || [];
            const localIndex = localLetters.findIndex(l => l.id === letter.id);
            if (localIndex !== -1) {
              localLetters.splice(localIndex, 1);
              uni.setStorageSync('xinxiang_letters', localLetters);
            }
          } catch (e) {
            console.warn('Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®Â§±Ë¥•', e);
          }
          
          uni.showToast({ title: 'Â∑≤Âà†Èô§', icon: 'success' });
        } catch (error) {
          uni.hideLoading();
          console.error('Âà†Èô§‰ø°‰ª∂Â§±Ë¥•:', error);
          uni.showToast({ 
            title: error.message || 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï', 
            icon: 'none' 
          });
        }
      } else {
        // Ê≤°ÊúâÂêéÁ´ØIDÔºåÂè™Âà†Èô§Êú¨Âú∞Êï∞ÊçÆ
        try {
          if (sourceIndex !== -1) {
            sourceList.splice(sourceIndex, 1);
          }
          uni.setStorageSync('xinxiang_letters', [...this.letters, ...this.sentLetters]);
          uni.showToast({ title: 'Â∑≤Âà†Èô§', icon: 'success' });
        } catch (e) {
          uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•', icon: 'none' });
        }
      }
    },
    
    // ÂéªÂÜô‰ø°
    goWrite() {
      uni.navigateTo({ url: '/subPackages/record/pages/xinxiang/create' });
    }
  }
};
</script>

